import React, { Component } from "react";
import { StyleSheet, View } from "react-native";
import App from "./src/App";

class Root extends Component {
  render() {
    return (
      <View style={styles.container}>
        <App />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#000",
    maxWidth: 400,
    margin: "auto"
  }
});

export default Root;



